import React, { Component } from "react";

import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import { Form, Input, Radio, Row, Col, Alert } from "antd";

//const { Option } = Select;
//const { TextArea } = Input;

class UnitUpdateForm extends Component {
  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        this.props.submit(values);
      }
    });
  };

  render() {
    // console.log(this.props);
    const { getFieldDecorator, getFieldError } = this.props.form;
    const { unit } = this.props;
    const { loading } = unit;
    const data = unit.unit ? unit.unit : [];
    const used = data.is_used;
    const error = unit.errorField ? unit.errorField : [];
    // console.log(data);

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    const radioGroupStyle = { 
      display: "flex", 
      width: "50%" 
    }

    const radioButtonStyle = { 
      flex: "1", 
      textAlign: "center" 
    }

    return (
      <Form layout="vertical" onSubmit={this.handleSubmit}>
        {used ? (
          <Row style={{ marginBottom: "20px" }}>
            <Col span={24}>
              <Alert
                message={<IntlMessages id="unit.help" />}
                type="warning"
                showIcon
              />
            </Col>
          </Row>
        ) : (
          ""
        )}
        {/* <Divider orientation="left" style={{ fontSize: 14 }}>
          <IntlMessages id="units.add.subtitle.data" />
        </Divider> */}
        <div className="isoDetailDataFlex" style={{ padding: "0" }}>
          <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 15px 0 0" }}>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="units.add.code" />}
              validateStatus={
                error.unit_code || getFieldError("unit_code")
                  ? "error"
                  : null
              }
              help={
                error.unit_code
                  ? error.unit_code
                  : getFieldError("unit_code")
              }
            >
              {getFieldDecorator("unit_code", {
                initialValue: data.unit_code,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  },
                  {
                    max: 30,
                    message: <IntlMessages id="maxLength30" />
                  }
                ]
              })(
                <Input
                  disabled={used ? true : false}
                  //style={{ textTransform: "uppercase" }}
                />
              )}
            </Form.Item>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="locations.add.name" />}
            >
              {getFieldDecorator("unit_name", {
                initialValue: data.unit_name,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  },
                  {
                    max: 50,
                    message: <IntlMessages id="maxLength50" />
                  }
                ]
              })(<Input disabled={used ? true : false} />)}
            </Form.Item>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="isDefault" />}
            >
              {getFieldDecorator("is_default", {
                initialValue: data.is_default ? data.is_default : 0,
              })(
                <Radio.Group style={radioGroupStyle} buttonStyle="solid">
                  <Radio.Button style={radioButtonStyle} value={1}><IntlMessages id="yes" /></Radio.Button>
                  <Radio.Button style={radioButtonStyle} checked value={0}><IntlMessages id="no" /></Radio.Button>
                </Radio.Group>
              )}
            </Form.Item>

          </div>
        </div>
  
        <Form.Item {...tailFormItemLayout}>
          <Button
            type="primary"
            htmlType="submit"
            style={{ marginTop: "20px", marginLeft: "50px" }}
            loading={loading}
            // disabled={used ? true : false}
          >
            <IntlMessages id="button.submit" />
          </Button>
        </Form.Item>
      </Form>
    );
  }
}

const WrappedUnitUpdateForm = Form.create({ name: "update_unit" })(UnitUpdateForm);

export default WrappedUnitUpdateForm;
